body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#root{
  height: 100%;
  min-height: 700px;
}
h1, h2{
  margin: 0;
  font-family: Helvetica;
}

.App {
  text-align: center;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Roboto";
  background-color: #708090;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#tooltip {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  text-align: center;
  width: 150px;
  height: 50px;
  padding: 2px;
  font: 12px;
  background: lightsteelblue;
  box-shadow: 1px 1px 10px;
  border-radius: 2px;
  pointer-events: none; 
}

.overlay {
  position: absolute;
  background: #fff;
  pointer-events: none; 
}


.container{
  flex-direction: column;
  padding: 20px 0px 20px 0px;
  align-self: center;
  position: relative;
  box-shadow: 2px 2px 20px;
  background-color: white;
  max-height: 90%;
  border-radius: 10px;
}

#title{
  font-size: 1em;
  -webkit-transform: translate(-320px,200px) rotate(270deg);
          transform: translate(-320px,200px) rotate(270deg);
}
